@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
body {
  margin: 0;
  font-family: "Goldman", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.garage-door{background-color:transparent;width:100%;height:100%;position:relative;overflow:hidden}.garage-door-left,.garage-door-right{width:calc(50% + 1px);height:100%;position:absolute;top:0}.garage-door-left{left:-50%}.garage-door-color-picker{position:absolute;bottom:10px;left:10px;display:flex;justify-content:center;align-items:center;max-width:45vh}.garage-door-color-picker label{margin-left:10px}.garage-door-right{right:-50%}.garage-door-unlock{width:100px;height:100px;border-radius:100%;position:absolute;top:calc(50% - 100px/ 2);right:calc(100% - 100px/ 2);z-index:1;display:flex;justify-content:center;align-items:center;font-size:calc(100px * 0.7);cursor:pointer}.garage-door-icon{position:absolute;bottom:0;right:0;-webkit-filter:invert(100%) opacity(10%) blur(1px);filter:invert(100%) opacity(10%) blur(1px);height:45vw;width:45vw;max-width:512px;max-height:512px}.garage-door-content{background-color:transparent;position:absolute;width:100%;height:100%}.garage-door-info{position:absolute;width:100%;height:calc(50% - 100px / 2 - 20px);background-color:transparent;display:flex;flex-direction:column;justify-content:center;align-items:center;font-size:4vh}

.project-viewer{position:relative;width:100%;height:100%}.project-viewer-title{position:absolute;top:50px;width:100%;text-align:center}.project-viewer-project{position:absolute;top:0;left:0;width:100%;height:100%;display:flex;justify-content:center;align-items:center}.select-wheel{position:relative;top:90vh;width:80vw;left:calc((100vw - 80vw) / 2)}.select-wheel-navigator{position:absolute;width:100%;display:flex;justify-content:space-between}.select-wheel-navigator>button{background-color:transparent;color:inherit;border:none;outline:none;font-size:calc(2vw + 2vh);display:flex;justify-content:center;align-items:center;max-width:calc(10vh - 8px);max-height:calc(10vh - 8px);width:10vw;height:10vw;border-radius:100px}.select-wheel-circle{position:absolute;width:80vw;height:80vw;border-radius:9999px;transition:transform 0.6s ease-in-out}@media only screen and (max-width: 600px){.select-wheel{width:96vw;left:calc((100vw - 96vw) / 2)}.select-wheel-circle{width:96vw;height:96vw}}.select-wheel-circle>div{position:absolute;text-align:center;font-size:4vh;width:100%;height:100%}.select-wheel-circle>div>div{margin-top:2vh}

.contact-me{position:absolute;left:10px;top:10px;display:flex}.contact-me>a{background-color:transparent;color:inherit;border:none;outline:none;height:60px;width:60px;display:flex;justify-content:center;align-items:center;margin:5px;font-size:45px;border-radius:100px;cursor:pointer;transition:box-shadow 0.05s linear}.my-infos{margin-top:95px;text-align:center}

